.app__writeview {
  margin-top: 5rem;
  min-height: 100vh;
  width: 60%;
  background-color: white;
  border-radius: 10px;
  margin-left: auto;
  margin-right: auto;

  .main {
    display: flex;
    flex-direction: column;
    align-items: center;

    .cover-img {
      display: flex;
      flex-direction: column;
      width: 100%;
      margin-top: 2rem;

      input {
        padding: 1rem;
        outline: none;
        border: solid 1px gray;
        border-right: none;
        border-left: none;
        border-top: none;

        &:focus {
          border-color: yellow;
        }
      }
    }

    .edit-title {
      margin-top: 2rem;
      width: 100%;
      height: auto;
      display: flex;
      justify-content: center;
      input {
        width: 90%;
        height: auto;
        outline: none;
        border: none;
        font-size: 3rem;
        font-weight: 800;
      }
    }

    .edit-content {
      width: 100%;
      height: auto;
      display: flex;
      justify-content: center;

      textarea {
        outline: none;
        border: none;
        width: 90%;
        min-height: 70vh;
        margin-top: 2rem;
        padding: 1rem;
        font-size: 1.1rem;
        word-spacing: 5px;
        line-height: 1.7rem;
      }
    }

    .menu {
      display: flex;
      margin: 2rem;
      margin-bottom: 2rem;
      width: 100%;

      .write-btn {
        cursor: pointer;
        padding: 0.5rem;
        margin: 0.5rem;
        margin-left: 2rem;
        background-color: rgb(57, 57, 56);
        color: white;
        border-radius: 5px;
        box-shadow: 0 0 5px gray;
        transition: all 0.2s ease-in-out;

        &:hover {
          background-color: white;
          color: black;
        }

        &:active {
          background-color: gray;
          color: white;
        }
      }

      .preview-btn {
        cursor: pointer;
        padding: 0.5rem;
        margin: 0.5rem;
        margin-right: auto;
        background-color: blue;
        color: white;
        border-radius: 5px;
        box-shadow: 0 0 5px gray;
        transition: all 0.2s ease-in-out;

        &:hover {
          background-color: white;
          color: black;
        }

        &:active {
          background-color: blue;
          color: white;
        }
      }

      .publish-btn {
        cursor: pointer;
        padding: 0.5rem;
        margin: 0.5rem;
        margin-right: 2rem;
        background-color: green;
        color: white;
        border-radius: 5px;
        box-shadow: 0 0 5px gray;
        transition: all 0.2s ease-in-out;

        &:hover {
          background-color: white;
          color: black;
        }

        &:active {
          background-color: green;
          color: white;
        }
      }
    }

    .preview {      
      min-height: 80vh;
      width: 100%;
      border-top-right-radius: 10px;
      border-top-left-radius: 10px;

      .preview-coverImg {
        margin-top: 2rem;
        img {
          width: 100%;
        }
      }

      .preview-title {
        font-size: 3rem;
        font-weight: 800;
        text-align: center;
      }

      hr {
        width: 70%;
        margin-top: 2rem;
        margin-bottom: 2rem;
      }

      .preview-content {
        margin: 1rem;
        padding: 1rem;
        word-spacing: 4px;
        line-height: 1.5rem;
      }      
    }
  }
}
